import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
    table: {
        minWidth: 650,
    },
});

function createData(name, value) {
    return { name, value };
}

const rows = [
    createData('Status', "Running"),
    createData('Location','Central US' ),
    createData('Subscription', "Platinum suite"),
    createData('Tags','Add Tag' ),
    createData('Leaner App', "wwww.learners.learningsapp.com"),
    createData('Instructor App','www.creators.learningsapp.com' )
    
];

export default function SimpleTable() {
    const classes = useStyles();

    return (
        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="simple table">
                
                <TableBody>
                    {rows.map(row => (
                        <TableRow key={row.name}>
                            <TableCell align="right"> </TableCell>  
                            <TableCell component="th" scope="row">
                                {row.name}
                            </TableCell>
                            <TableCell align="left">{":\t" + row.value}</TableCell>
                            <TableCell align="left"> </TableCell>
                            <TableCell align="left"> </TableCell>
                            <TableCell align="left"> </TableCell>
                            <TableCell align="left"> </TableCell>
                            <TableCell align="left"> </TableCell>
                            <TableCell align="left"> </TableCell>
                            <TableCell align="left"> </TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}